.emptyCart {
  margin: auto;
  text-align: center;
  padding: 10vmax;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.emptyCart > svg {
  font-size: 5vmax;
  color: rgb(128, 0, 57);
}
.emptyCart > p {
  font-size: 2vmax;
}
.emptyCart > a {
  background-color: rgb(51, 51, 51);
  color: white;
  border: none;
  padding: 1vmax 3vmax;
  cursor: pointer;
  font: 400 1vmax "Roboto Slab";
  text-decoration: none;
}

.cartPage {
  padding: 5vmax;
}

.cartHeader {
  background-color: rgb(128, 0, 57);
  width: 90%;
  box-sizing: border-box;
  margin: auto;
  color: white;
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
}
.cartHeader > p {
  margin: 20px;
  font: 400 1.2vmax "Roboto Slab";
}
.cartHeader > p:last-child {
  text-align: end;
}

.cartContainer {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
}

.cartInput {
  display: flex;
  align-items: center;
  height: 8vmax;
}

.cartInput > button {
  border: none;
  background-color: rgba(0, 0, 0, 0.616);
  padding: 0.5vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}
.cartInput > button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}

.cartInput > input {
  border: none;
  padding: 0.5vmax;
  width: 2vmax;
  text-align: center;
  outline: none;
  font: 400 1vmax "Roboto Slab";
  color: rgba(0, 0, 0, 0.74);
}

.cartSubtotal {
  display: flex;
  padding: 0.5vmax;
  height: 8vmax;
  align-items: center;
  box-sizing: border-box;
  font: 400 1vmax;
  justify-content: flex-end;
  color: rgba(0, 0, 0, 0.753);
}

.cartGrossTotal {
  display: grid;
  grid-template-columns: 2fr 1.2fr;
}

.cartGrossProfitBox {
  border-top: 2px solid #303030;
  /* border-bottom: 1px solid rgb(128, 0, 57); */
  margin: 1vmax 4vmax;
  box-sizing: border-box;
  padding: 1vmax 0;
  font: 600 1.2vmax "Roboto Slab";
  color: #303030;
  display: flex;
  justify-content: space-between;
}

.checkOutBtn {
  display: flex;
  justify-content: flex-end;
}
.checkOutBtn > button {
  background-color: rgb(128, 0, 57);
  color: white;
  border: none;
  padding: 1vmax 4vmax;
  width: 50%;
  font: 300 1vmax "Roboto Slab";
  margin: 2vmax 4vmax;
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .cartPage {
    padding: 0;
    margin-bottom: 20vmax;
    min-height: 60vh;
  }

  .cartHeader {
    width: 100%;
    font: 300 1.7vmax "Roboto Slab";
    grid-template-columns: 3fr 1fr 1fr;
  }

  .cartContainer {
    width: 100%;
    grid-template-columns: 3fr 1fr 1fr;
  }

  .cartInput {
    height: 20vmax;
  }

  .cartInput > button {
    padding: 1vmax;
  }

  .cartInput > input {
    width: 3vmax;
    padding: 0.5vmax;
    font: 400 1.8vmax "Roboto Slab";
  }

  .cartSubtotal {
    padding: 1.5vmax;
    height: 20vmax;
    font: 300 2vmax "Roboto Slab";
  }

  .cartGrossTotal {
    display: grid;
    grid-template-columns: 0fr 2fr;
  }

  .cartGrossProfitBox {
    padding: 2vmax;
    font: 800 2vmax "Roboto Slab";
  }

  .checkOutBtn > button {
    padding: 2vmax 4vmax;
    width: 100%;
    font: 300 2vmax "Roboto Slab";
  }
}
